export default {"name":"重庆沱茶","img":"\\heicha\\chongqingtuocha\\img_20191122132533z36iny.png","desc":["重庆沱茶，重庆茶厂于1953年开始生产，属上乘紧压茶。制作时选用中上等晒青、烘青和炒青毛茶，运用传统工艺和现代化生产手段，对原料进行搭配、筛分、整形，再进行大拼堆、称料、蒸制、揉袋压形。其成品茶形似碗臼，色泽乌黑油润，汤色澄黄明亮，叶底较嫩匀，滋味醇厚甘和，香气馥郁陈香。含有对人体有益的咖啡碱、茶多酚、矿物质等多种元素。五十年以来，重庆沱茶由盛而衰，道路艰难，发展瓶颈很多，在困境中缓慢发展。"],"menus":[{"name":"重庆沱茶茶叶历史","content":[{"text":"重庆沱茶1980年起出口日本、意大利、香港等 10多个国家和地区。1983年8月在罗马举行的第22届世界食品评选会上，经大会常务组织委员会评定：\"峨眉牌沱茶，经实验室分析测验及独立评判委员会对产品有机生化、工艺过程、外观和产品商品的审定证明，本品完全符合有关规定\"，被授予金质奖章。1985年12月荣获国家优质产品银质奖。"},{"text":"解放初期"},{"text":"刘邓大军在重庆组建了西南区茶叶公司，成立不久后，1951年8月改建成重庆茶厂。这是当时全国最大的茶叶加工企业，其投资达到了1000多万元，职工2000多人，年产量7000多吨。作为全国"},{"text":"最大的茶叶加工企业，当时的重庆茶厂享尽了计划经济的各种好处。西南地区的茶原料云集重庆茶厂，由国家实行统购统销，重庆茶厂从来就不缺茶叶原材料，更不用担心生产出来的茶叶没有人买，就连自己的职工想买茶叶也得找主管部门批条子。1952年，国家曾用了 4000吨重庆茶叶与苏联换回了136架战斗机，这让他一直觉得很骄傲。据他回忆说，当时的大米才几分钱一斤，而茶叶则可以卖到两三元一斤。这样的价格差距，凸显了茶叶对当时以农业为主的中国经济的重要性。 “那个时候，中央号召要建立500个年产量1万担(一担相当于现在的100斤)以上的茶叶基地。”阙成清告诉信报记者说，最初重庆有茶园10万亩，到上个世纪70年代，茶叶种植面积翻了一番，达到20万亩。"},{"text":"上个世纪50年代初"},{"text":"重庆茶厂开始研制生产重庆沱茶，这款茶叶以川东、川南地区14个产茶区的优质茶叶为原料，经精制加工而成，属于上乘紧压茶。当时的加工技术比较落后，茶叶生产基本上属于半作坊式。直至上世纪70年代，重庆茶厂才拥有了一条立体生产线， “这条立体生产线是从意大利进口，当时的价值约为700万元左右。”据了解，在那一时期，全国只有两条立体生产线，除重庆茶厂外，另一条生产线也落户在重庆。获得立体生产线对于重庆茶厂而言意义重大，这使得重庆茶厂在当时快速超越了其他茶叶生产企业。当时大多数茶叶生产企业仍然是手工作坊生产，相对于这些企业而言，重庆茶厂的立体生产线大大提高了生产速度，而且标准化程度更高：“每一包成品茶叶的大小、重量以及口味都差不多，这是手工作坊难以做到的。”阙成清说。这条生产线极大地提高了重庆沱茶的质量，技术改造使重庆沱茶将其他茶叶远远落在了后面。到上世纪70年代末，重庆茶厂的沱茶年销售量已达8000 多吨，数倍于云南“下关”沱茶产量。"},{"text":"经过近30年的发展"},{"text":"重庆沱茶声名远扬。1983年8月23日，在意大利罗马举办的第22届世界优质食"},{"text":"品评选大会上，重庆茶厂生产的“峨眉牌重庆沱茶”获得金质奖，这是建国后中国获得的第一块茶叶类世界金奖。获奖后，重庆沱茶的发展更是如日中天，其他各种奖项也纷至沓来，其口碑声名，再一次远超“下关”沱茶。与此同时，重庆沱茶的生产能力也达到一个顶峰，最高时年产量达到2000吨，其营业额若以如今的市值计算，已经达到了近1亿元。而且，随着国际声誉的建立，重庆沱茶已经走出国门，出口到欧洲、日本等30多个国家，出口金额每年达到了600万美元。"},{"text":"国际金奖"},{"text":"重庆沱茶获得这枚国际金奖，对重庆茶厂并无意外可言，阙成清对当时的重庆沱茶充满信心：“这款积聚了计划经济时代所有宠爱的茶叶，从它诞生的那一天起，就远远超过了其他茶叶。”那一时期，对重庆市民而言，外形酷似圆面包、底下看似厚壁碗的重庆沱茶是大家再熟悉不过的了：“当时，火车、轮船到达重庆时，广播里最先介绍的特产就是重庆沱茶”。"}]},{"name":"重庆沱茶茶叶特点","content":[{"text":"重庆沱茶品质特点：其成品茶形似碗臼，色泽乌黑油润，汤色澄黄明亮，叶底较嫩匀，滋味醇厚甘和，香气馥郁陈香。"},{"src":"\\heicha\\chongqingtuocha\\img_20191122132533xx13q6.jpg"}]},{"name":"重庆沱茶加工工艺","content":[{"text":"沱茶是紧压茶中最好的一种。"},{"text":"是以晒青毛茶作原料加工而成的。"},{"src":"\\heicha\\chongqingtuocha\\img_201911221325333tgjjc.jpg"},{"text":"制作过程："},{"text":"1、制坯。"},{"text":"2、蒸压成型。"},{"text":"3、干燥。"},{"text":"产品特点：沱茶为碗臼型，色泽暗绿露毫，香气清正，滋味浓厚甘和，汤色黄明，叶底嫩匀。茶中含梗为3%，灰分7%，杂子0.2%。"}]},{"name":"重庆沱茶储藏方法","content":[{"text":"一、放置地点以一楼接近地面为佳，目的是茶可以自然和空气接触，借湿度温度醇化!"},{"text":"二、住公寓者有品茗间，可放茶。辟茶室存放，不要让除湿机或是冷气机吸干茶的水分，应以自然温度存放最佳!"},{"text":"三、存放空间地点防止日晒或是阳光照射。这也是存放关健。光线对成茶破坏力强大，要小心!"},{"text":"四、不宜在外层再包上塑料袋，要是已经剥开了，要放入茶瓮。"},{"text":"五、茶瓮的材质有瓷器，陶器两大类。用瓷瓮置茶得留心，熟沱带酸气不宜放入这种容器，应入陶瓮较好。瓷瓮放生饼为宜;陶瓮还分上釉与不上釉。上釉的瓮保温度较佳，活性适中，可放入陈茶。不上釉的陶瓮转茶性更佳，宜放入新茶!"}]},{"name":"重庆沱茶冲泡方法","content":[{"text":"先将沱茶掰成碎块，也可用蒸汽蒸热后一次性把沱茶解散晾干，每次取3克，用开水冲泡5分钟后饮用。"},{"src":"\\heicha\\chongqingtuocha\\img_201911221325331xxeap.jpg"},{"text":"也有将掰成碎块的沱茶放入小瓦罐中在火膛上烤香后冲进沸水烧胀后饮用，还可在煮烤沱茶的小瓦罐中加入油、盐、糖后饮用。"}]},{"name":"重庆沱茶功效作用","content":[{"text":"1、解油腻牛羊毒，括肠通泄。"},{"text":"2、降低胆固醇的效果则与安妥明相似，且长期饮用无副作用。"},{"text":"3、常饮沱茶，对降低人体所含三酸甘油脂、胆固醇、血尿酸等有不同程度作用。"},{"text":"4、沱茶有杀死癌细胞、抗突变、防癌功能及减肥降血脂作用。"}]},{"name":"重庆沱茶获奖情况","content":[{"text":"1983年获奖后，重庆沱茶的发展如日中天，其他各种奖项也纷至沓来，其口碑声名远超“下关”沱茶。"}]}]}